import React from "react"
import styles from "./card.module.css"
import {Link} from "gatsby"

const Card = ({}) => {
    return (
        <Link to="/blog/resize-observer">
        <div className={styles.cardContainer}>
            <div className={styles.cardTitle}>Detecting when an element changes size with ResizeObserver</div>
            <div className={styles.cardDescription}>ResizeObserver is a JavaScript web API that allows you to run code whenever an element's size changes</div>
        </div>
        </Link>
    )
}

export default Card;